.dashboard-container {
  display: flex;
  flex-direction: row;
  gap: 16px;

  .client-progress-container {
    flex: 1 1 50%;
    display: block;
    height: calc(100vh - 170px);
    // height: 100%;
    overflow: hidden;
    width: 100%;
    min-width: 0;
    // max-width: 700px;
    // overflow-y: scroll;

    // margin-left: 16px;
    padding: 16px;

    border-radius: 16px;
    // border: 1px solid #c4c6d0;
    // background-color: #faf9fd;

    // .title {
    //   font-size: 18px;
    // }

    // .client-progress {
    //   height: calc(100vh - 600px);
    // }

    .scrollable-container {
      height: calc(100vh - 238px);
      overflow-y: scroll;
    }

    .programs-list-wrapper-placeholder {
      height: calc(100vh - 238px);
      overflow-y: hidden;

      .program-list-skeleton {
        border-radius: 8px;
        width: 100%;
        height: 78px;
        overflow: hidden;
        position: relative;
        margin-bottom: 4px;

        &::after {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          transform: translateX(-100%);
          animation: shimmer 1s infinite;
          content: '';
        }

        @keyframes shimmer {
          100% {
            transform: translateX(100%);
          }
        }
      }
    }
  }

  .dashboard-fields {
    width: 100%;
    min-width: 0;
    flex: 1 1 50%;
    display: flex;
    flex-direction: column;
    gap: 16px;
    height: calc(100vh - 170px);

    // width: 50%;
    // background: red;
    border-radius: 16px;

    ui-favorite-programs {
      flex: 1;
      min-height: 0;
      // overflow: hidden;
    }

    ui-favorite-exercises {
      flex: 1;
      min-height: 0;
      // overflow: hidden;
    }

    .favorites-field-skeleton {
      flex: 1;
      border-radius: 16px;
      overflow: hidden;
      position: relative;

      &::after {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        transform: translateX(-100%);
        animation: shimmer 1s infinite;
        content: '';
      }

      @keyframes shimmer {
        100% {
          transform: translateX(100%);
        }
      }
    }
  }

  @include respond(compact) {
    flex-direction: column;
    height: calc(100vh - 80px);
    display: block;

    .client-progress-container {
      height: fit-content;
      width: 100%;
      border-radius: 0;
      padding: 8px;
      margin-bottom: 16px;

      & h3 {
        padding: 16px 16px 0 16px;
      }

      .scrollable-container {
        height: fit-content;
        overflow-y: visible;
      }
    }

    .dashboard-fields {
      height: 400px;

      .placeholder-block {
      }
    }
  }
}
